	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/cynin-desktop/index.md absPath of file >>> MarkdownRemark","html":"<p>Cyn.in is an AIR-based communications.. um.. thing?  It seems designed for group collaborative projects, with things like status messages and file sharing.  Cyn.in stands for 'sign in' (I got it, eventually. - I don't think phonetic puns work very well for things that are only written down).  Cyn.in is developed by Cynapse [<a href=\"http://www.cynapse.com/products/cynin\">http://www.cynapse.com/products/cynin</a>], but it also available as an open source project, which you can download all the source for.  Personally, I'm not too impressed by it's current state because a) it doesn't seem very useful to me and b) I'm not impressed by the source code and c) the AIR application is a bit.. useless.  The Cyn.in web application is quite impressive, but from a desktop version I would have expected more desktop integration - half of the buttons in Cyn.in desktop redirect to the website.</p>\n<p>Sorry Cynapse - it's a nice idea but I'm a bit nonplussed, especially given the hype that got generated for it.</p>\n<p>[\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/cyn-in2-d6ec92f136cfa5f35395cc9b1d9b5bdc-e8574.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 80%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"cyn-in2\"\n        title=\"\"\n        src=\"/static/cyn-in2-d6ec92f136cfa5f35395cc9b1d9b5bdc-e8574.jpg\"\n        srcset=\"/static/cyn-in2-d6ec92f136cfa5f35395cc9b1d9b5bdc-c94d9.jpg 158w,\n/static/cyn-in2-d6ec92f136cfa5f35395cc9b1d9b5bdc-9885b.jpg 315w,\n/static/cyn-in2-d6ec92f136cfa5f35395cc9b1d9b5bdc-e8574.jpg 500w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Of course, why not go make your own mind up:<a href=\"http://blogs.adobe.com/air/2008/12/adobe_air_powered_cynin_deskto.html\"></a><a href=\"http://blogs.adobe.com/air/2008/12/adobe_air_powered_cynin_deskto.html\">http://blogs.adobe.com/air/2008/12/adobe_air_powered_cynin_deskto.html</a></p>","frontmatter":{"title":"Cyn.in Desktop","date":"January 08, 2009","tags":["cynin"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/using-google-analytics-in-flash/","tags":["actionscript","flash","google-analytics"],"title":"Using Google Analytics in Flash","date":"2009-01-06T09:20:18.000Z"}},"next":{"frontmatter":{"path":"/blog/visualizing-large-data-sets-in-flexflash/","tags":["flex","data-visualization"],"title":"Visualizing LARGE Data Sets in flex/flash","date":"2009-01-09T08:45:51.000Z"}}}}

